<script>
  export let jp_props;
  let props = {};
  let self_closing_tags = [
    "hr",
    "input",
    "area",
    "base",
    "br",
    "col",
    "embed",
    "img",
    "link",
    "meta",
    "param",
    "source",
    "track",
    "wbr",
  ];
  import Dummy from "./Dummy.svelte";
  import Chart from "./Chart.svelte";
  import Switch from "./Switch.svelte";
  let components = {
    Dummy: Dummy,
    ChartJS: Chart,
    Switch: Switch,
    Dummy: Dummy,
  };
  //event_handler.js requires props
  props["jp_props"] = jp_props;

  $: description_object = {
    style: jp_props.style,
  };
  $: if ("attrs" in jp_props) {
    for (const [key, value] of Object.entries(jp_props.attrs)) {
      description_object[key] = value;
    }
  }
  $: if (jp_props.classes) {
    description_object["class"] = jp_props.classes;
  }

  let is_self_closing = false;
  if (self_closing_tags.indexOf(jp_props.html_tag) > -1) {
    is_self_closing = true;
  }

  function forwardEventHandler(event) {
    //forward event to justpy eventHandler
    console.log("event handler called");
    console.log(event.type);
    eventHandler(props, event, false);
  }
  function registerEventHandlers(node) {
    for (let i = 0; i < jp_props.events.length; i++) {
      node.addEventListener(jp_props.events[i], forwardEventHandler);
    }
    return {
      destroy() {
        for (let i = 0; i < jp_props.events.length; i++) {
          node.removeEventListener(jp_props.events[i], forwardEventHandler);
        }
      },
    };
  }
</script>

{#if jp_props.vue_type === "html_component"}
  <!-- svelte's syntax for input differs from other html components -->
  {#if is_self_closing}
    <!-- <input  {...description_object} on:input={input_eh} on:change={change_eh}> -->
    <svelte:element
      this={jp_props.html_tag}
      {...description_object}
      use:registerEventHandlers
    />
  {:else}
    <svelte:element
      this={jp_props.html_tag}
      {...description_object}
      use:registerEventHandlers
    >
      {#if jp_props.text}
        {jp_props.text}
      {/if}

      {#each jp_props.object_props as cobj_props}
        {#if cobj_props.show}
          <svelte:self jp_props={cobj_props} />
        {/if}
      {/each}
    </svelte:element>
  {/if}
{:else}
  <!-- if component is not a html component; svelte syntax differs for html vs svelte component  -->
  <svelte:component
    this={components[jp_props.vue_type]}
    {...description_object}
    {jp_props}
  />
{/if}
